<template>
  <div class="login-box">
    <div class="login">
      <div>
        <h1 style="color:#409EFF;">信贷管理系统</h1>
        <br />
        <h4>Credit Management System</h4>
        <br />
      </div>
      <el-input style="margin:3px;" placeholder="请输入用户名" v-model="user" clearable></el-input>
      <el-input style="margin:3px;" placeholder="请输入密码" v-model="passwd" show-password></el-input>
      <button id="login" @click="Login">登录</button>
      <ul class="test-user">
        <li style="display:flex;">
          <input type="checkbox" v-model="alreayRead" />我已阅读
          <b style="color:#409EFF;cursor:pointer;" @click="Open">用户协议</b>!
        </li>
        <li>
          <label style="color:#409EFF;fontWeight:bold;">测试号登录：</label>
          <input type="radio" name="user" @change="admins" />管理员
          <input type="radio" name="user" @change="sales" />销售员
          <input type="radio" name="user" @change="firsts" />初审员
        </li>
      </ul>
    </div>

    <div class="User-agreement" v-show="show">
      <div class="content">
        <h1>用户协议须知</h1>
        <br />
        <br />
        <br />
        <b>1：登录人员的要求</b>
        <br />
        <p>本系统未提供注册功能，需要登录请向管理人员申请通过后方可登录，擅自登录的人员会予以警告和降职的处罚。</p>
        <br />
        <b>2：登录人员的温馨提示</b>
        <br />
        <p>如果您对系统已经操作完了，请您主动退出登录系统，以便其他人员进行登录，实行严格的规则，请您谅解。</p>
        <p>如要得到更多的规则，请翻看入职手册！</p>
        <br />
        <b>3：温馨提示</b>
        <p style="color:red;">项目是个人完成，内部相关信息如有相同，纯属巧合！合同内部内容纯属虚构，不具有任何意义，如果内部功能用不了，可能是后端数据已经被删除！</p>
        <button @click="Close" id="close">收到</button>
      </div>
    </div>
  </div>
</template>

<script>
import { LoginFn } from "@/utils/api";
export default {
  data() {
    return {
      user: "",
      passwd: "",
      alreayRead: false,
      show: false
    };
  },
  mounted() {
    this.$confirm("您可以使用测试号登录系统！", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning",
      center: true
    })
  },
  methods: {
    admins() {
      this.user = "admin";
      this.passwd = "admin@123";
    },
    sales() {
      this.user = "fgfg";
      this.passwd = "1234";
    },
    firsts() {
      this.user = "Test66";
      this.passwd = "123123";
    },
    Login() {
      if (!this.alreayRead) {
        this.$message({
          message: "请先勾选用户协议！",
          type: "warning"
        });
      } else {
        if (this.user.length >= 10 || this.passwd.length >= 10) {
          alert("用户名和密码必须小于10位！");
        } else if (!this.user.length || !this.passwd.length) {
          alert("用户名和密码不能为空！");
        } else {
          LoginFn({ account: this.user, password: this.passwd })
            .then(res => {
              // console.log(res.data);
              if (res.data.code == 20000) {
                this.$router.push("/home");
                this.$message({
                  title: "登录成功",
                  message: "恭喜您登录成功！",
                  type: "success"
                });
                sessionStorage.setItem("token", res.data.data.token);
              } else {
                this.$message.error("用户名或者密码错误！请再检查。");
              }
            })
            .catch(err => {
              // console.log(err);
            });
        }
      }
    },
    Open() {
      this.show = true;
    },
    Close() {
      this.show = false;
    }
  }
};
</script>
<style scoped>
.test-user {
  width: 100%;
  display: flex;
  list-style: none;
  justify-content: space-between;
  font-size: 14px;
}
</style>